import Button from "antd/lib/button";
import Modal from "antd/lib/modal";
import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router";
import { setShowModalConfirm } from "redux/slices/global";
import styled from "styled-components";

const ModalConfim = ({
  decs,
  title,
  method,
  data,
  isLoading,
  img,
  type,
  textButtonFooter,
  url,
}) => {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const { showModalConfirm } = useSelector((state) => state.globalReducer);

  const _onSubmit = () => {
    dispatch(
      method({
        ...data,
      })
    );
  };

  const Footer = (valueType) => {
    switch (valueType) {
      case "Redirect":
        return (
          <CustomFooter>
            <CustomButtonCancel
              size="large"
              onClick={() => {
                dispatch(setShowModalConfirm(false));
              }}
            >
              No
            </CustomButtonCancel>
            <CustomButtonSave
              size="large"
              htmlType="submit"
              loading={isLoading}
              style={{ marginLeft: "1rem", marginRight: 0 }}
              onClick={() => {
                dispatch(setShowModalConfirm(false));
                setTimeout(() => {
                  navigate(url || "/");
                }, 500);
              }}
            >
              {textButtonFooter || "Confirm"}
            </CustomButtonSave>
          </CustomFooter>
        );

      default:
        return (
          <CustomFooter>
            <CustomButtonSave
              size="large"
              htmlType="submit"
              loading={isLoading}
              onClick={() => {
                _onSubmit();
                dispatch(setShowModalConfirm(false));
              }}
            >
              Yes
            </CustomButtonSave>
            <CustomButtonCancel
              size="large"
              onClick={() => {
                dispatch(setShowModalConfirm(false));
              }}
            >
              No
            </CustomButtonCancel>
          </CustomFooter>
        );
    }
  };

  return (
    <CustomModal
      title="Confirm"
      open={showModalConfirm}
      onCancel={() => {
        dispatch(setShowModalConfirm(false));
      }}
      width={400}
      footer={null}
    >
      <CustomContent>
        <img alt="" src={img} />
        <Title>{title}</Title>
        <Decs>{decs}</Decs>
      </CustomContent>
      {Footer(type)}
    </CustomModal>
  );
};

export default ModalConfim;

const CustomFooter = styled.div`
  display: flex;
  justify-content: flex-end;
  margin-top: 34px;
`;

const CustomButtonSave = styled(Button)`
  background-color: ${(props) => props.theme.main};
  color: #fff;
  margin-right: 16px;
  border-radius: 4px;

  :hover {
    background: ${(props) => props.theme.darker};
    color: #fff;
  }
`;

const CustomButtonCancel = styled(Button)`
  background-color: #fff;
  border-radius: 4px;

  :hover {
    background: ${(props) => props.theme.darker};
    color: #fff;
  }
`;

const CustomContent = styled.div`
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;

  img {
    width: 70px;
    margin-bottom: 16px;
  }
`;

const Title = styled.span`
  font-family: var(--roboto-500);
  font-size: 18px;
  line-height: 26px;
  color: #2c2c2c;
  text-align: center;
`;

const Decs = styled.span`
  font-size: 14px;
  line-height: 22px;
  text-align: center;
  color: #595959;
`;

const CustomModal = styled(Modal)`
  .ant-modal-header {
    border-radius: 10px 10px 0 0;
    background: #f2f4f5;
    padding: 7px 24px;
  }

  .ant-modal-title {
    font-family: var(--roboto-700);
  }

  .ant-modal-close-x {
    height: 36.6px;
    line-height: 36.6px;

    .anticon {
      color: #141414;
    }
  }

  .ant-modal-content {
    border-radius: 10px;
  }
`;
